import React, { useState } from 'react';
import './App.css';
import { ThemeProvider, CssBaseline } from "@material-ui/core";
import { createMuiTheme } from "@material-ui/core/styles";
import HeaderAppBar from './components/Header/HeaderComponent';
import WallpapersComponent from './components/AllWallpapers/AllWallpapersComponent';
import BottomNavigationBar from './components/BottomNavigation/BottomNavigationComponent';
import AboutSection from './components/AboutPage/AboutComponent';
import { Fab } from '@material-ui/core';
import ThemeToggle from '@material-ui/icons/InvertColorsTwoTone';
import { light, dark } from './Themes';
import { Switch, Route } from 'react-router-dom';
import SearchBar from './components/Header/SearchBarComponent';


function App() {
  
  const [theme, setTheme] = useState(true);
  
  const primaryTheme = dark;

  const appliedTheme = createMuiTheme(theme ? primaryTheme : (primaryTheme === light ? dark : light));
  return (
    
      <ThemeProvider theme={appliedTheme}>
        <CssBaseline />
        <HeaderAppBar />
        <Switch>
        <Route exact path='/' 
        render={() => 
        <div>
            <Fab style={{position: "fixed", top: "4px", right: "4px", padding: "auto", zIndex: "99999", boxShadow: "none", backgroundColor: "rgba(0,0,0,0)"}} onClick={() => setTheme(!theme)} variant="round" size="small" color="primary">
              <ThemeToggle />
            </Fab>
            <WallpapersComponent />
        </div> 
        } />
        <Route path='/search' 
        render={() => 
        <div>
            <SearchBar />
            <WallpapersComponent />
        </div> 
        } />
        <Route exact path='/about' 
        render={() => 
        <div>
            <Fab style={{position: "fixed", top: "4px", right: "4px", padding: "auto", zIndex: "99999", boxShadow: "none", backgroundColor: "rgba(0,0,0,0)"}} onClick={() => setTheme(!theme)} variant="round" size="small" color="primary">
              <ThemeToggle />
            </Fab>
            <AboutSection />
        </div> 
        } />
        </Switch>
        <BottomNavigationBar />
      </ThemeProvider>
  );
}

export default App;
