import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import { Fab } from '@material-ui/core';
import GetWallpaper from '@material-ui/icons/GetAppTwoTone';
import wallpaperList from './wallpaperList';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    backgroundColor: theme.palette.background.paper,
    paddingBottom: '120px',
    marginTop: '-6px !important'
  },
  gridList: {
    width: '100%',
    height: '100%'
  },
  gridElement: {
    width: 'auto',
    height: '320px',
    margin: '2px',
    padding: '0px !important',
    position: 'relative'
  },
  gridElementImage: {
    width: '100%',
    padding: '0px !important',
    margin: '0px !important',
    left: '0px !important',
    height: '320px',
    objectFit: 'cover'
  },
  gridListTileBar: {
    backgroundColor: 'rgba(0,0,0,0.5)',
    width: '100%',
    height: '64px',
    position: 'absolute',
    bottom: '0px'
  },
  gridListTileBarSubtitle: {
    color: 'rgba(255,255,255,0.75)'
  },
  icon: {
    color: 'rgba(255, 255, 255, 0.75)',
    backgroundColor: 'rgba(0,0,0,0)',
    boxShadow: 'none',
    marginRight: '12px',
    
    '&:hover': {
      background: 'rgba(0,0,0,0)'
    },
  },
}));

function WallpapersComponent() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      
      <Grid container className={classes.gridList}>
        {wallpaperList.map((tile) => (
          <Grid item xs={6} sm={4} md={3} lg={2} key={tile.url} className="searchContent">
            <Card align="center" variant="outlined" className={classes.gridElement}>
            <img src={tile.thumbnail} alt={tile.name} className={classes.gridElementImage} />
            <CardContent>
            <GridListTileBar
              align={'left'}
              className={classes.gridListTileBar}
              title={tile.name}
              subtitle={<span className={classes.gridListTileBarSubtitle}>{tile.author}</span>}
              actionIcon={
                <form method="get" action={tile.url}>
                        <Fab aria-label={`download ${tile.title}`} type="submit" variant="round" size="small" className={classes.icon}><GetWallpaper /></Fab>
                </form>
              }
            />
            </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>
    </div>
  );
}

export default WallpapersComponent;